.conteiner {
  width: 1200px;
  margin: 0 auto;
  padding: 20px 15px;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin: -10px;
  display: flex;
}

.video-card {
  border: 1px solid #a5a1a1;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px;
  display: flex;
  box-shadow: 2px 2px 2px #beb5b5;
}

.video-card img {
  width: 100%;
  height: 170px;
  background-size: cover;
  display: block;
}

@media screen and (min-width: 320px) {
  .video-card {
    width: 100vw;
  }
}

@media screen and (min-width: 425px) {
  .video-card {
    width: 400px;
  }
}

@media screen and (min-width: 1440px) {
  .video-card {
    width: calc(25vw - 30px);
  }
}

